import common from '../../../common/common.js'

export default {
	data() {
		return {
			userid:'',
			shopInfo:[],
			shop_lat:'',
			shop_lng:'',
		};
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		that.shopInfo = shopInfo
		
	},
	methods:{
		
		//提交
		formSubmit(e){
			var that = this
			var data = e.detail.value;
			
			
			
			if(data.password == ''){					
				uni.showToast({
					title: '请输入原密码',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			if(data.newpassword.length < 6){
				uni.showToast({
					title: '新密码至少6位',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			if(data.renewpassword.length < 6){
				uni.showToast({
					title: '确认新密码至少6位',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			if(data.newpassword != data.renewpassword){
				uni.showToast({
					title: '两次新密码不一致',
					duration: 1500,
					icon :'none'
				});
				return false
			}				
			
			uni.showLoading({
				title: '处理中'
			});
			
			uni.request({
				url: common.sjApiUrl + 'merchant/updateShopPassword',
				method: 'GET',
				data: {
					shop_id:that.shopInfo.shop_id,
					password:data.password,
					newpassword:data.newpassword,
					renewpassword:data.renewpassword,
				},
				success: res => {
					uni.hideLoading();
					
					if(res.data.code == 0){
						var shopInfo = res.data.userInfo;
						uni.setStorageSync('shopInfo',shopInfo);
						that.shopInfo = shopInfo
						
						uni.showToast({
							title: '操作成功',
							duration: 1500,
							icon:'success'
						});
						setTimeout(function(){
							uni.removeStorageSync('shopInfo');
							uni.reLaunch({
								url: '/pages/shop/userlogin/userlogin',
								success: res => {
									
								},
								fail: () => {},
								complete: () => {}
							});
						},1500)
					}else{
						uni.showToast({
							title: res.data.msg,
							duration: 1500,
							icon:'none'
						});
					}
					
				},
				fail: () => {},
				complete: () => {}
			});
			
			
			
		}
	}
}
